@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,500;1,200&display=swap);
body {
    font-family: 'Raleway', sans-serif;
    color: rgb(120, 120, 120);
    -webkit-font-smoothing: antialiased;
}

/* Animaciones */
.animated {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

.fast {
    -webkit-animation-duration: 0.4s;
    animation-duration: 0.4s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.fadeIn {
    -webkit-animation-name: fadeIn;
            animation-name: fadeIn;
}


.bg-navbar{
    background: linear-gradient(to bottom, rgb(37, 41, 52) 0%,rgb(37, 41, 52) 100%) ;
}

.language {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
}

.language img {
    width: 30px;
    height: 30px;
    border-radius: 100%;
    margin: 5px 0;
    -webkit-transform: scale(1) !important;
            transform: scale(1) !important;
}

.progress {
    position: relative;
    height: 20px;
}

.progress > .progress-type {
    width: 30%;
    position: absolute;
    left: 0;
    font-weight: 800;
    padding: 3px 30px 2px 10px;
    color: rgb(255, 255, 255);
    background-color: rgba(30, 120, 50, 0.8);
}

.progress > .progress-completed {
    position: absolute;
    right: 0;
    font-weight: 800;
    padding: 3px 10px 2px;
}

.progress > .progress-bar {
    background-color: rgba(30, 150, 50, 0.8);
}

.companyImage {
    border-top-left-radius: 0.25rem;
}

.isActive{
    border-color: rgba(30, 150, 50, 0.2);
    box-shadow: 0 0 0 0.25rem rgba(30, 150, 50, 0.2);
}

.card{
    object-fit: cover;
}

.isActive {
    border: 5px solid rgba(30, 150, 50, 0.2);
    box-shadow: 0 0 0 0.25rem rgba(30, 150, 50, 0.2);
}

.cardExperience {
    box-shadow: 5px 5px 5px #eee;
}

.companyImage {
    width: 100%;
    height: 225px;
    object-position: 0 center;
    object-fit: contain;
}

.flex {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

section:nth-of-type(1) {
    height: calc(90vh + 53px);
    padding: 0 10px;
}

.canvas {
    position: absolute;
}

.text {
    display: flex;
    flex-direction: column;
    text-align: left;
    color: #fff;
    margin: 0 0 20px 0;
    pointer-events: none;
    z-index: 1;
}

.welcomeText {
    margin-bottom: 20px;
    font-size: 14pt;
}

.highlight {
    font-size: 40pt;
    font-weight: 800;
    color: yellow;
}

.slogan {
    font-size: 20pt;
    font-weight: 600;
    color: #ddd;
}

@media screen and (max-width: 600px) {
    .text {
        font-size: 20pt;
    }
}

#particle-canvas {
    width: 100%;
    height: 105vh;
    background: linear-gradient(to bottom, rgb(37, 41, 52) 0%,rgb(37, 41, 52) 100%);
    vertical-align: middle;
}

.interest-image {
    align-items: center;
    cursor: auto;
    background-size: cover;

}

.interest-text {
    padding: 16px 32px 14px;
}


.root {
    margin-top: 8px;
    margin-bottom: 4px;
}

.images {
    margin-top: 8px;
    display: flex;
    flex-wrap: wrap;
}

.imageWrapper {
    position: relative;
    display: block;
    padding: 0;
    border-radius: 0;
    height: 40vh;
    cursor: auto;
}

.imageWrapper:hover {
    z-index: 1;
}

.imageWrapper:hover .imageBackdrop {
    opacity: 0.15;
}

.imageWrapper:hover .imageMarked {
    opacity: 0
}

.imageWrapper :hover .imageTitle {
    border: 4px solid currentColor;
}

.imageButton {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
}

.imageSrc {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-size: cover;
    background-position: center 40%;
}

.imageBackdrop {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: black;
    opacity: 0.5;
    transition: opacity;
}

.imageTitle {
    position: relative;
    padding: 8px;
}


.cardTraining {
    box-shadow: 5px 5px 5px #eee;
}

.cardTraining img {
    width: 100%;
    object-position: 0 center;
    object-fit: contain;
}


.accordion-button:focus {
    box-shadow: 0 0 0 0 transparent !important;
}

.accordion-button:not(.collapsed) {
    color: rgba(30, 150, 50, 1);
    background-color: transparent;
}

.activeCard {
    border-color: rgba(30, 150, 50, 0.2) !important;
    box-shadow: 0 0 0 0.25rem rgba(30, 150, 50, 0.2) !important;
}

.companyImg {
    height: 215px;
    display: flex;
    align-items: center;
    overflow: hidden;
}

.footer {
    background: linear-gradient(to bottom, rgb(37, 41, 52) 0%,rgb(37, 41, 52) 100%) ;
}

.line {
    height:2px;
    background-color:rgb(206,206,206);
    width:100%;
}

